@import "../../variables";

.section-form {
  background: linear-gradient(to bottom, #2d394a, #0a1620d6, $bgHeader);
}

.section-form-title {
  text-align: center;
  color: #ffffff;
  padding: 30px 0 30px;
  margin: 0;
}

.form-wrapper-none {
  visibility: hidden;
}

.form-wrapper {
  visibility: visible;
  margin: 0 auto;
  background-color: #ffffff;
  max-width: 1000px;
  & > * {
    padding: 1em;
  }
  @media (min-width: 735px) {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

.form-company-info {
  background: linear-gradient(to bottom, #2e4a5f51, #0010257b),
    url(../../../public/img/background-contact-form1.jpg);
  @include bgcover(center);

  color: #ffffff;
  line-height: 1.6;

  @media (min-width: 735px) {
    background: linear-gradient(to bottom, #0028484e, #00000066),
      url(../../../public/img/background-contact-form1.jpg);
    @include bgcover(center);
  }
  & h3 {
    margin: 0 0 1rem 0;
    text-align: center;
    @media (min-width: 735px) {
      text-align: left;
    }
  }
  & ul {
    width: max-content;
    list-style: none;
    padding-left: 0;
    text-align: center;
    margin: 0 auto;
    @media (min-width: 735px) {
      margin: 0;
      text-align: left;
    }
  }
}

.form-svg {
  vertical-align: middle;
}
// FORM STYLES

.form-contact form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  @media (min-width: 475px) {
    grid-template-columns: 1fr 1fr;
  }

  & label {
    display: block;
    margin: 0;
    width: 100%;
    font-size: 14px;
    color: #485e74;
    &.error-form {
      color: #ff00009c;
    }
  }
  & p {
    margin: 0;
  }
  & input,
  & button,
  & textarea {
    color: #485e74;
    width: 100%;
    padding: 1em;
    border: 1px solid #c9e6ff;
    border-radius: 4px;
  }
  & input:focus,
  & textarea:focus {
    box-shadow: 0 0 8px 0 #485e74b3;
    outline: none;
  }
  & .full {
    @media (min-width: 475px) {
      grid-column: 1/3;
    }
  }
  & .form-submit {
    background: #37465a;
    border: none;
    font-weight: bold;
    color: #f8f8f8;
    font-size: 1em;
    text-transform: uppercase;
    cursor: pointer;
    padding: 12px;

    &:hover {
      background: #485e74d5;
      color: #fff;
      outline: none;
      transition: background-color 0.9s ease-out;
    }
  }
}

.form-field {
  &.error {
    border: 1px solid #f64b4b50;
  }
}
